<template>
  <div class="wrapper wrapper-content">

    <div class="ibox float-e-margins">
      <div class="ibox-title">
        <h5>基本信息</h5>
      </div>

      <div class="ibox-content">
        <edit-form v-bind:hasOperation="hasOperation" v-bind:formTitle="'基本信息'" v-bind:data="form" v-bind:keys="keys1" @confirm="edit"></edit-form>
        <edit-form v-bind:hasOperation="hasOperation" v-bind:formTitle="'主要负责人'" v-bind:data="form" v-bind:keys="keys2" @confirm="edit"></edit-form>
        <edit-form v-bind:hasOperation="hasOperation" v-bind:formTitle="'通讯方式'" v-bind:data="form" v-bind:keys="keys3" @confirm="edit"></edit-form>
      </div>
    </div>

  </div>
</template>

<script type="text/ecmascript-6">
import Cache from '@/assets/cache'
import Service from '@/assets/service/service'
export default {
  name: 'collegeIntroductionBasic',
  data () {
    return {
      hasOperation: false,
      form: {},
      keys1: [
        { label: '学部名称', key: 'name' },
        { label: '英文名称', key: 'english_name' }
      ],
      keys2: [
        { label: '学部主任', key: 'principal_director' },
        { label: '主任助理', key: 'principal_assistant' },
        { label: '行政秘书', key: 'principal_secretary' },
        { label: '资料管理员', key: 'principal_administrator' }
      ],
      keys3: [
        { label: '固定电话', key: 'contact_tel' },
        { label: '传真', key: 'contact_fax' },
        { label: '地址', key: 'contact_address' }
      ]
    }
  },
  methods: {
    edit (form) {
      Service.EssentialInformation.edit(this.form.id, form).then(response => {
        if (response.code == 200) {
          this.getDetails()
        }
      })
    },
    getDetails () {
      Service.EssentialInformation.get().then(response => {
        if (response.code == 200) {
          this.form = response.data
        }
      })
    }
  },
  created () {
    if (Cache.hasPermission('basic_information')) {
      this.hasOperation = true
    }
    this.getDetails()
  }
}
</script>

<style scoped>

</style>



